/* ================== GLOBAL ================== */
body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: linear-gradient(135deg, #ff6600, #000000);
  min-height: 100vh;
  color: white;
}

/* ================== LINK STYLE ================== */
form a {
  color: white;
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}

/* ================== FORM AUTH ================== */
.auth-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

form {
  max-width: 400px;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.05);
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

input {
  padding: 10px;
  border: none;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, 0.2);
  color: white;
}
input::placeholder {
  color: #ddd;
}

button {
  padding: 10px;
  background-color: #ff6600;
  border: none;
  border-radius: 5px;
  color: white;
  font-weight: bold;
  cursor: pointer;
  transition: background 0.3s;
}
button:hover {
  background-color: #e65c00;
}

/* ================== TOGGLE LINK ================== */
.toggle-button {
  margin-top: 1rem;
  background: transparent;
  border: none;
  color: white;
  cursor: pointer;
  text-decoration: underline;
  font-size: 14px;
}

/* ================== LANGUAGE SWITCHER ================== */
.language-switcher {
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 1000;
}
.language-select {
  color: black;
  border: 1px solid #ccc;
  padding: 6px 12px;
  border-radius: 6px;
  font-weight: bold;
  background-color: white;
  font-size: 14px;
}

/* ================== FEEDBACK MESSAGES ================== */
.success-message {
  color: #4caf50;
  text-align: center;
}
.error-message {
  color: #ff4444;
  text-align: center;
}

/* ================== NAVBAR ================== */
nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4px 6px;
  background: rgba(0, 0, 0, 0.6); /* agar tetap kontras saat scroll */
  backdrop-filter: blur(8px);     /* efek transparan modern */
  font-size: 16px;
  height: auto;
}

.nav-left {
  display: flex;
  align-items: center;
  padding-left: 20px; /* tambahkan jarak dari kiri */
}
/* LOGO RESPONSIVE */
.logo-img {
  height: 60px;
  display: block;
  width: auto;
  object-fit: contain;
}

/* Responsive untuk perangkat kecil */
@media (max-width: 768px) {
  .logo-img {
    height: 40px;
  }
}

.nav-center,
.nav-right {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}
.nav-left {
  font-weight: bold;
  font-size: 18px;
}
.nav-center a,
.nav-right a {
  color: white;
  text-decoration: none;
  transition: color 0.3s ease;
}
.nav-center a:hover,
.nav-right a:hover {
  color: #ffcc99;
}

.user-dropdown {
  position: relative;
}

.user-button {
  background: transparent;
  border: none;
  color: white;
  cursor: pointer;
  font-size: 16px;
  display: flex;
  align-items: center;
  gap: 5px;
}

.dropdown-menu {
  position: absolute;
  top: 130%;
  right: 0;
  background-color: #222;
  padding: 10px 15px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  border-radius: 5px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.3);
  z-index: 9999;
}

.dropdown-menu a,
.dropdown-menu button {
  color: white;
  text-decoration: none;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 14px;
  text-align: left;
}

.dropdown-menu a:hover,
.dropdown-menu button:hover {
  color: #ffcc99;
}

/* ================== LAYOUT WRAPPER ================== */
.layout-wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: linear-gradient(135deg, #ff6600, #000000);
}
.spacer {
  height: 64px;
}

/* ================== HERO SECTION (Optional if reused) ================== */
.home-section {
  background-size: cover;
  background-position: center;
  padding: 80px 20px;
  color: white;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.hero-text {
  background-color: rgba(0, 0, 0, 0.6);
  padding: 30px;
  border-radius: 15px;
  max-width: 800px;
  text-align: center;
}
.hero-text h1 {
  font-size: 36px;
  margin-bottom: 20px;
}
.hero-text p {
  font-size: 18px;
  line-height: 1.6;
}
.hero-buttons {
  margin-top: 30px;
  display: flex;
  justify-content: center;
  gap: 20px;
}
.wa-button {
  background-color: white;
  color: black;
  padding: 12px 20px;
  border-radius: 30px;
  display: flex;
  align-items: center;
  font-weight: bold;
  text-decoration: none;
}
.wa-button img {
  width: 24px;
  margin-right: 10px;
}
.layanan-button {
  background-color: #ffffff;
  color: #ff6600;
  padding: 12px 20px;
  border-radius: 30px;
  font-weight: bold;
  text-decoration: none;
}
